.customers-story .aside__block-modules__img img { height: 25px; }

.customers-story__text { padding-left: 80px; }

@media only screen and (max-width: 1040px) { .customers-story__text { padding-left: 20px; } }

@media only screen and (max-width: 767px) { .customers-story__text { padding-left: 0; } }

.customers-story__text h3 { font-size: 30px; font-weight: 300; line-height: 1.5; color: #9e9e9e; margin-bottom: 50px; max-width: 700px; }

@media only screen and (max-width: 767px) { .customers-story__text h3 { font-size: 24px; text-align: center; } }

.customers-story__text h3 span { color: #ec135c; }

.customers-story__text h2 { font-size: 30px; font-weight: 300; line-height: 1.33; color: #131a2e; margin-bottom: 30px; margin-top: 20px; }

@media only screen and (max-width: 767px) { .customers-story__text h2 { font-size: 24px; text-align: center; } }

.customers-story__text strong { font-weight: 600; font-size: 14px; color: #131a2e; }

@media only screen and (max-width: 767px) { .customers-story__text strong { text-align: center; display: block; } }

.customers-story__text p, .customers-story__text li { font-size: 14px; font-weight: 300; line-height: 2.14; color: #131a2e; margin-bottom: 40px; }

@media only screen and (max-width: 767px) { .customers-story__text p { text-align: center; } }

.customers-story__text li { margin-bottom: 10px; }

.customers-story__text ul { list-style: disc; padding-left: 20px; margin-bottom: 40px; }

.customers-story__quote { position: relative; padding-left: 220px; }

@media only screen and (max-width: 1040px) { .customers-story__quote { padding-left: 120px; } }

@media only screen and (max-width: 767px) { .customers-story__quote { padding-left: 50px; } }

.customers-story__quote img { position: absolute; left: 80px; }

@media only screen and (max-width: 1040px) { .customers-story__quote img { left: 20px; width: 70px; } }

@media only screen and (max-width: 767px) { .customers-story__quote img { left: 0; width: 35px; } }

.customers-story__quote p { font-size: 25px; font-weight: 300; line-height: 1.8; color: #131a2e; margin-bottom: 40px; }

@media only screen and (max-width: 767px) { .customers-story__quote p { font-size: 18px; text-align: center; } }

.customers-story__quote b { font-size: 16px; font-weight: 600; line-height: 1.88; color: #131a2e; }

@media only screen and (max-width: 767px) { .customers-story__quote b { text-align: center; display: block; } }

.customers-story.souq .customers-story__intro { height: 400px; padding-top: 50px; }
